import axios from 'axios'

const post = (url) => {
    return (params) => {
        return new Promise((resolve, reject) => {
            if(process.env.NODE_ENV !== 'development'&&process.env.NODE_ENV=='production'){
                url = 'http://101.34.51.116:3000'+url;
            }
            axios.post(url,{
                params
            }
            ).then((res) => {
                // console.log(url, res)
                const data = res.data
                // console.log(data)
                if (data.err === false) {
                    resolve(data)
                } else {
                    // console.log(err)
                   
                }
            }).catch((err) => {
                reject(err)
            })
        })

    }

}



export{
    post
}